
































import { VotdSymbolId } from "@/symbols";
import Vue from "vue";

export default Vue.extend({
  name: "SymbolCard",

  props: {
    symbol: {
      type: Object,
      required: true,
    },
    width: {
      type: Number,
    },
    editing: {
      type: Boolean,
    },
  },

  computed: {
    redacted: {
      get() {
        return this.symbol.id === VotdSymbolId.REDACTED;
      },
    },
  },

  methods: {
    updateName(newName: string) {
      const symbol = this.symbol.clone();
      symbol.name = newName;
      this.$emit("input", symbol);
    },
  },
});
